.TodoSearch {
  display: flex;
  justify-content: end;
}

input {
  border-radius: .4rem;
  border: 1px solid #A5A5A5;
  padding: .3rem .7rem;
  background-color: rgb(247, 247, 247);

}

input::placeholder {
  color: #A5A5A5;
  opacity: 25%;
}

input:focus {
  outline-color: #61DAFA;
}